body, html{
    margin: 0;
    padding: 0;font-family: Arial, Helvetica, sans-serif;
}

* {
    box-sizing: border-box;
  }

.page-container {
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: fit-content;
    position: relative;
  }

:root{
    --pr-color:#32cd32;
    --sc-color:rgb(80, 128, 96);
}

/* Base styles for the div */
body {
  width: 100vw;
  max-width: 100vw;
  background-color: #f9f9f9;
}

/* Styles for screens larger than laptops (1024px and up) */
@media (min-width: 1024px) {
  .app {
    width: 320px; /* Fixed mobile size */
  }
}
